import React, { useEffect, useState } from 'react';
import axios from 'axios';
import 'bpg-nino-elite-exp-caps/css/bpg-nino-elite-exp-caps.min.css';
import './styles/currencyApp.scss';

function Currency({currencyData}) {
  return (
    <div className="app-inner__currencyWrapper-item">
      {currencyData.cur_name} → {currencyData.cur_value} ლარი
    </div>
  )
}

function CurrencyApp() {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    axios('https://any.ge/currency/api.php?ids=42,14,35')
      .then(response => {
        setData(response.data)
      })
      .catch(error => {
        console.log("Error fetching data: ", error);
      })
      .finally(() => {
        setLoading(false);
      })
  }, [])

  const currencies = loading ? null :
    data.map(elem => <Currency key={elem.id} currencyData={elem}/>);

  return (
    <div className="app app-outer">
      <div className="app-inner">
        <div className="app-inner__title">
          ვალუტის კურსი
        </div>

        <div className="app-inner__currencyWrapper">
          {loading ? "Loading..." : currencies}
        </div>
      
      </div>
    </div>
  );
}

export default CurrencyApp;
